body {
  color: #333;
  background: #f9f9f9;
  max-width: 600px;
  margin: auto;
  padding: 2rem;
  font-family: system-ui, sans-serif;
}

h1, h2 {
  text-align: center;
}

form {
  flex-direction: column;
  gap: .5rem;
  margin-bottom: 2rem;
  display: flex;
}

input, textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: .5rem;
  font-size: 1rem;
}

button {
  color: #fff;
  cursor: pointer;
  background: #4caf50;
  border: none;
  border-radius: 4px;
  padding: .5rem;
  font-size: 1rem;
}

button:hover {
  background: #45a049;
}

ul {
  padding: 0;
  list-style: none;
}

li {
  background: #fff;
  border-left: 5px solid #4caf50;
  margin-bottom: .5rem;
  padding: .75rem;
}

.timestamp {
  color: #888;
  margin-top: .25rem;
  font-size: .8rem;
  display: block;
}
